


































































// 引入外部组件与ts
import { Component, Prop, Vue, Emit } from "vue-property-decorator";
import { ElForm } from "element-ui/types/form";
@Component({})
export default class EditDialog extends Vue {
  @Prop({ type: Object, required: false, default: () => {} })
  arg!: any;
  @Prop({ type: Boolean, required: true, default: true })
  add!: Boolean;

  singleOption: any = {
    name: "",
    typeCode: "",
    config: {
      options: [],
      numMin: 0,
      numMax: 10,
      imgMaxNum: 9,
      presetText: ""
    }
  };
  addOptionsDialog: Boolean = false;
  itemOptions: Array<any> = [
    {
      value: "select",
      label: "单项选择"
    },
    {
      value: "multiSelect",
      label: "多项选择"
    },
    {
      value: "number",
      label: "数值"
    },
    {
      value: "image",
      label: "图片"
    },
    {
      value: "text",
      label: "文本"
    },
    {
      value: "location",
      label: "定位"
    }
  ];
  formRules: any = {
    name: { required: true, message: "请输入项目名称", trigger: "change" },
    typeCode: { required: true, message: "请选择项目类型", trigger: "change" }
  };

  created() {
    if (this.add) {
      this.singleOption = {
        name: "",
        typeCode: "",
        config: {
          options: [],
          numMin: 0,
          numMax: 10,
          imgMaxNum: 9,
          presetText: ""
        }
      };
    } else {
      this.singleOption = JSON.parse(JSON.stringify(this.arg));
    }
    this.addOptionsDialog = true;
  }

  // 数据项类型修改
  typeChange() {
    if (this.singleOption.typeCode === "select" || this.singleOption.typeCode === "multiSelect") {
      this.singleOption.config.options = [];
    } else if (this.singleOption.typeCode === "number") {
      this.$set(this.singleOption.config, "numMin", 0);
      this.$set(this.singleOption.config, "numMax", 10);
    } else if (this.singleOption.typeCode === "text") {
      this.$set(this.singleOption.config, "presetText", "");
    } else if (this.singleOption.typeCode === "image") {
      this.$set(this.singleOption.config, "imgMaxNum", 1);
    }
  }

  addOptionItem() {
    this.singleOption.config.options.push({});
  }
  removeOptionItem(item: any) {
    let index = this.singleOption.config.options.indexOf(item);
    if (index !== -1) {
      this.singleOption.config.options.splice(index, 1);
    }
  }
  changeVisibleFalse() {
    this.$emit("close");
  }
  addOptionsToList() {
    (this.$refs.form as ElForm).validate(isValid => {
      if (isValid) {
        switch (this.singleOption.typeCode) {
          case "select":
          case "multiSelect":
            if (this.singleOption.config.options.length === 0) {
              this.$message.info("请添加选项");
              return;
            }
            break;
          case "number":
            if (
              this.singleOption.config.numMin === "" ||
              this.singleOption.config.numMin === undefined ||
              this.singleOption.config.numMax === "" ||
              this.singleOption.config.numMax === undefined
            ) {
              this.$message.info("请设置最大值或最小值");
              return;
            }
            break;
          case "image":
            if (this.singleOption.config.imgMaxNum === "" || this.singleOption.config.imgMaxNum === undefined) {
              this.$message.info("请设置图片的最大张数");
              return;
            }
            break;
        }
        this.$emit("addArg", this.singleOption);
      } else {
        return false;
      }
    });
  }

  beforeDestroy() {
    this.singleOption = null;
  }
}
