import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import EditDialog from "./components/EditDialog.vue";
import api from "@/microApps/eventCenter/api";
import { ElForm } from "element-ui/types/form";

@Component({
  components: { EditDialog }
})
export default class EventTemplateEdit extends Vue {
  preData: any = {};
  // eventBasicForm: any = {}; // 基础属性
  submitForm: any = {
    name: "",
    remark: "",
    code: "",
    items: []
  };
  submitFormRules: any = {
    name: { required: true, message: "请输入模板名称", trigger: "change" },
    code: { required: true, message: "请输入模板编码", trigger: "change" }
  };
  inspectProject: any[] = [];
  showDeleteButton: Boolean = true;
  showAddDialog: Boolean = false;
  isAdd: Boolean = false; // 是否是新增模式，true：新增， false：修改
  singleArg: any = {}; // 传入子组件参数
  index: any = null;

  async created() {
    let id: any = this.$route.query.id;
    if (id) {
      await this.getTemplateById(id);
      this.preData.items.map((item: any) => {
        this.inspectProject.push({
          argTemplate: item.argTemplate ? item.argTemplate : { presetText: null },
          config: item.argTemplate ? item.argTemplate : { presetText: null },
          name: item.name,
          typeCode: item.typeCode
        });
      });
    }
    this.submitForm = this.preData;
  }

  async getTemplateById(id: string) {
    let {
      data: { data, status, message }
    } = await this.$http.get(api.getTemplateById + "/" + id);
    if (status) {
      this.preData = data;
    } else {
      this.$message.error(message);
    }
  }
  // 打开编辑弹窗-添加
  addArg() {
    this.showAddDialog = true;
    this.isAdd = true; // 是否是新增模式，true：新增， false：修改
  }
  // 打开编辑弹窗-修改
  editItem(item: any) {
    this.index = this.inspectProject.indexOf(item);
    this.isAdd = false;
    this.showAddDialog = true;
    this.singleArg = item;
  }
  // 将配置项插入数组
  addSingleArg(value: any) {
    // 修改配置项适应list展示效果
    value.config.default = [];
    value.config.options
      .filter((item: any) => item.defaultChosen)
      .map((i: any) => {
        value.config.default.push(i.content);
      });
    value.config.defaultItem = value.config.default.join("");
    value.argTemplate = value.config; // 转json字符串用于提交服务器
    if (this.isAdd) {
      this.$set(this.inspectProject, this.inspectProject.length, value); // 新增时插入
    } else {
      this.$set(this.inspectProject, this.index, value); // 编辑时替换
    }
    this.showAddDialog = false;
    this.submitForm.items = this.inspectProject;
  }
  deleteItem(item: any) {
    let index = this.inspectProject.indexOf(item);
    if (index !== -1) {
      this.inspectProject.splice(index, 1);
    }
    this.submitForm.items = this.inspectProject;
  }

  save() {
    (this.$refs.submitForm as ElForm).validate(async isValidate => {
      if (isValidate) {
        let id = this.$route.query.id;
        let {
          data: { data, status, message }
        } = await this.$http.post(api[id ? "updateEventTemplate" : "addEventTemplate"], this.submitForm);
        if (status) {
          this.$message.success(id ? "修改成功" : "添加成功");
          this.$router.back();
        } else {
          this.$message.error(message);
        }
      }
    });
  }
}
